.valores.active .section-title{
  opacity: 1 !important;
  transform: translateY(0) !important;
}

.valores{
  .section-title{
    margin-bottom: 2em;
    opacity: 0;
    transform: translateY(-500%);
    transition: all .8s;
  }
  h3{
    font-size: 2em;
    margin-top: 1em;
    margin-bottom: 1.5em !important;
  }
  .section__container{
    padding: var(--header-height) 1em 1em;
    justify-content: center;
  }
  p{
    font-size: 1.4em !important;
    line-height: 1.2 !important;
    &.marco{
      $bd: 3px solid $color-fisrt;
      text-align: center;
      position: relative;
      font-size: 1.75em;
      transition: all .8s;
      transform: translateX(-150%);
      font-family: 'Gotham-Medium', sans-serif;
      width: 650px;
      margin-bottom: 1em;
      @media screen and (orientation: portrait) and (max-width: 800px){
        font-size: 1em;
        width: 90%;
        margin: 0 auto;
      }
      &::after, &::before{
        content: '';
        height: 1.5em;
        width: 1.5em;
        position: absolute;
        background-color: transparent;
        top: -.3em;
        left: -.5em;
        border-top: $bd;
        border-left: $bd;
      }
      &::before{
        border-right: $bd;
        left: unset;
        right: -.5em;
        border-left: unset;
      }
    }
  }
}
.valores p {
  transform: translateX(100%);
  transition: all .8s;
}
.active .valores p, .active .marco{
  transform: translateX(0) !important;
}

