.comunicacion {
  background: url("../img/recursos-m.png") top center / cover no-repeat;

  @media screen and (orientation: landscape) {
    background: url("../img/recursos.png") top center / cover no-repeat;
  }

  &>.section__container {
    justify-content: center;
    align-items: flex-start;
    // padding-right: 50%;
    // @media screen and (orientation: portrait) {
    //   padding-right: 1em;
    // }
  }
  & .section-title{
    color: #fff;
  }
  img{
    width: 35%;
    opacity: .75;
  }
}

.comunicacion_text-container {
  max-height: 30vh;
  overflow-y: auto;
  padding-right: 1em;
  margin-bottom: 5vh;

  @media screen and (orientation: landscape) and (min-width: 1080px),
  (max-width: 1400px) {
    width: 50%;

  }

  @media screen and (orientation: landscape) and (min-width: 1400px) {
    width: 40%;
  }
}

.comunicacion_text-container::-webkit-scrollbar {
  width: 10px;
}

/* Estilos barra (thumb) de scroll */
.comunicacion_text-container::-webkit-scrollbar-thumb {
  background: darkred;
  border-radius: 10px;
}

.comunicacion_text-container::-webkit-scrollbar-thumb:active {
  background-color: #999999;
}

.comunicacion_text-container::-webkit-scrollbar-thumb:hover {
  background: darkred;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

/* Estilos track de scroll */
.comunicacion_text-container::-webkit-scrollbar-track {
  background: #111;
  border-radius: 10px;
  border: 3px solid #111;
  box-shadow: 0 0 0 3px #111;
}

.comunicacion_text-container::-webkit-scrollbar-track:hover,
.comunicacion_text-container::-webkit-scrollbar-track:active {
  background: #111;
}

.comunicacion-img-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5vw;
  height: 20vh;
  width: 100%;

  .img {
    width: 100%;
    height: inherit;
  }

  img {
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
  }
}
.lineas{
  $size: 70px;
  width: 50vw;
  height: $size;
  position: relative;
  display: flex;
  overflow: hidden;
  .l-cont{
    width: 100%;
    height: 100%;
    transform: translateX(-$size);
  }
  .linea{
    position: absolute;
    left: 0;
    top: 0;
    width: $size;
    height: $size;
    $bd: 12px;
    background: transparent;
    transform-origin: center;
    transform: rotate(-45deg);
    color: #DF1A1F;
    //border-width: 50px;
    //border-right: solid;
    //border-top: solid;
    box-shadow: $bd $bd #DF1A1F;
    margin-left: $bd;
    @for $i from 1 through 22{
      &:nth-child(#{$i}){
        left: $bd*($i*3);
      }
    }

  }
}
.comunicacion_text-container{
  width: 100%;
  overflow: auto;
  position: relative;
  z-index: 75;
}
