.responsabilidad{
  .section-title{
    color: #fff;
    letter-spacing: 0;
    &.second{
      letter-spacing: 1vw;
      color: $color-fisrt;
    }
  }
  &__grid{
    display: grid;
    @include from(l){
      position: relative;
      grid-template-columns: 60% 1fr;
      grid-gap: 2vw;
      height: 58vh;
    }

  }
  &__text{
    @media screen and (max-width: 1400px) {
      max-height: 90%;
      overflow-y: auto;
    }
  }
  &__img{
    display: grid;
      grid-template-rows: 1fr auto;
    @include from(l){
      grid-gap: 4vh;
      height: inherit;
    }

  }
  img{
    display: none;
  }
  &__img__container{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    position: relative;
    @include to(l){
      justify-content: space-around;
    }
    img{
      position: absolute;
      display:block;
      width: auto;
      height: 40%;
      @include to(l){
        position: unset;
      }
      &:nth-child(1){
        transform: translateX(-33.33%);
        @include to(l){
          transform: unset;
        }
      }
      &:nth-child(2){
        top: 50%;
        transform: translateX(33%) translateY(-50%);
        @include to(l){
          transform: unset;
          display: none;
        }
      }
      &:nth-child(3){
        bottom: 0;
        transform: translateX(-33.33%) ;
        @include to(l){
          transform: unset;
        }
      }
    }
  }
  .texto-resaltado{
    color: red;
    text-align: center;
    font-weight: bold;
    line-height: 1.2;
    font-size: 2.8vh !important;
  }
}
.mobile{
  .marcaagua{
    position: relative;
    display: block;
  }
  .responsabilidad__grid{
    height: 60vh;
    grid-template-rows: 1fr 35%;
    position: relative;
  }
  .responsabilidad__img{
    grid-gap: .5em;
  }
  .responsabilidad__img__container{
    height:100%;
    img{
      height:100%;
      position: absolute;
      top: 0;
      left: 50%;
      bottom: unset;
      transform: translateX(50%);
      &:first-child{
        transform: translateX(-150%)
      }
    }
  }
}
