*,
*::before,
*:after {
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}

html,
body {
  width: 100%;
  margin: 0;
}

body {
  font-family: var(--body-font);
  background: $body-bg;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  max-width: var(--width100);
  overflow-x: hidden;
  overflow-y: hidden;
  font-size: 14px;
  color: $font-color;
}

a {
  color: inherit;
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

img,
picture,
picture img {
  display: block;
  max-width: 100%;
  height: auto;
}

section,
figure,
article {
  margin: 0;
  padding: 0;
}
p {
  margin-bottom: 1em;
  &:last-child,
  &:only-child,
  &:last-of-type,
  &:only-of-type {
    margin-bottom: 0;
  }
}
body, a, button {
  cursor:url('../img/cursor.cur'),
  url('../img/cursor.png'),
  url('../img/cursor.gif'),
  default !important;
}
.section{
  min-height: 100vh;
}
