.main-header{
  padding: 0 2vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  //border-bottom: 1px solid rgba(#fff,.1);
  height: var(--header-height);
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  > a {
    display: block;
    width: auto;
    height: 70%;
    @media screen and (orientation: portrait) {
      height: 80%;
    }
  }
  img{
    height: 100%;
    width: auto;
  }
  .contact-link{
    display: inline-block;
    margin-left: 0em;
    transform-origin: top center;
    transform: rotate(90deg);
    line-height: 1;
    text-transform: uppercase;
    font-size: .8em;
  }
}
