.equipos_text-container{
  max-height: 35vh;
  overflow-y: auto;
  padding-right: 1em;
  margin-bottom: 5vh;
  @media screen and (orientation: landscape) and (min-width: 1080px), (max-width: 1400px) {
    width: 100%;
  }
  //@media screen and (orientation: landscape) and (min-width: 1400px) {
  //  width: 40%;
  //}
  @include to(l){
    width: 100%;
  }
}
.scroll-ele{
  position: relative;
  //z-index: var(--z-fixed);
  z-index: 100;
}

.equipos_text-container::-webkit-scrollbar {
  width: 10px;
}

/* Estilos barra (thumb) de scroll */
.equipos_text-container::-webkit-scrollbar-thumb {
  background: darkred;
  border-radius: 10px;
}

.equipos_text-container::-webkit-scrollbar-thumb:active {
  background-color: #999999;
}

.equipos_text-container::-webkit-scrollbar-thumb:hover {
  background: darkred;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

/* Estilos track de scroll */
.equipos_text-container::-webkit-scrollbar-track {
  background: #111;
  border-radius: 10px;
  border: 3px solid #111;
  box-shadow: 0 0 0 3px #111;
}

.equipos_text-container::-webkit-scrollbar-track:hover,
.equipos_text-container::-webkit-scrollbar-track:active {
  background: #111;
}

.equipos-img-container{
  display: grid;
  grid-template-columns: repeat(5,1fr);
  grid-gap: 1em;
  height: 20vh;
  width: 100%;
  .img{
    width: 100%;
    height: inherit;
    &:nth-child(2), &:nth-child(3), &:nth-child(5), {
      @include to(m){
        display: none;

      }
    }
  }
  @include to(m){
    grid-template-columns: repeat(2,1fr);

  }
  @include from(768px){
    .mobile &{
      grid-template-columns: repeat(3, 1fr);
      .img{
        display: none;
        &:first-child, &:nth-child(4), &:nth-child(2){
          display: block;
        }
      }
    }
  }
  img{
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
  }
}
.dron{
  max-width: 400px;
  margin: 1em auto;
