button{
  &:focus{
    box-shadow: none;
    outline: none;
  }
}
.hamburger {
  font-size: 16px;
  border: 2px solid;
  border-radius: 50%;
  height: 3.5em;
  width: 3.5em;
  transform-origin: center;
  transform: scale(.8);
}

.hamburger-box {
  transform-origin: center;
  transform: scale(.6) translateX(-15px) translateY(-2px);

  .mobile & {
    transform: scale(.6) translateX(-17px) translateY(-4px);

  }
}

.hamburger-inner,
.hamburger-inner::after,
.hamburger-inner::before {
  background-color: $font-color !important;
}


.hamburger.is-active:hover,
.hamburger:hover {
  opacity: 1 !important;
}

.main-header-left {
  display: flex;
  align-items: center;

  a {
    margin-left: .5em;
    transform-origin: center;
    text-transform: uppercase;
    transform: rotate(90deg);
    line-height: 1em;
    font-size: .8em;

    .mobile & {
      line-height: 1;
      font-size: .8em;
    }
  }
}
