.agro {
  background: url("../img/agro-m.png") top center / cover no-repeat;

  @media screen and (orientation: landscape) {
    background: url("../img/agro.png") top center / cover no-repeat;
  }

  &>.section__container {
    justify-content: center;
    align-items: flex-start;
  }
  .section-title{
    color: #fff;
    letter-spacing: 0;
    &.second{
      letter-spacing: 1vw;
      color: $color-fisrt;
    }
  }
  &-grid{
    display: grid;
    @include from(l){
      grid-template-columns: 60% 40%;
      grid-gap: 2em;
    }
    @include to(m){
      height: 80vh;
      grid-template-rows: 60% 1fr;
      grid-template-columns: unset;
      //height:90%;

    }
    @media screen and(min-height: 900px) and (orientation: portrait){
      height: 70vh;
    }

  }
  &-text{
    width: 100%;
  }
  &__img{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 35%;
      @include from(m){
        $tras : 35%;
        position: absolute;
        transform-origin:center;
        &:first-child{
          transform: translateY(-$tras) translateX(-$tras);
        }
        &:last-child{
          transform: translateY($tras) translateX($tras);
        }
      }
      @include to(m){
        width: 40%;
        height:auto;
        top: unset;
        left: 1em;
        right: unset;
        bottom: 0;
        &:last-child{
          left:unset;
          right: 1em;
        }
      }

    }
  }
}
@include from(768px){
  .mobile {
    .agro-grid{
      grid-template-rows: auto 1fr;
    }
    .equipos_text-container{
      //margin-bottom: 0;
      height: 300px;
    }
    .agro__img{
      display: flex;
      justify-content: space-between;
      transform: translateY(1em) scale(.8) !important;
      img{
        position: relative;
        width: auto;
        height: 100% !important;
      }
    }
  }
}
