.contacto {
  background: url("../img/contacto-m.png") top center / cover no-repeat;
  font-family: 'Gotham-Medium', sans-serif;

  @media screen and (orientation: landscape) {
    background: url("../img/contacto.png") top center / cover no-repeat;
  }
  h3{
    font-family: inherit;
    width: 100%;
    text-align: center;
    font-size: 1.2em;
  }

  &>.section__container {
    justify-content: center;
    align-items: flex-start;
    padding-bottom: 1em;
    position: relative;
    @include from(l){
      transform-origin: center;
    }
  }
  & .section-title {
    color: #fff;
  }
  &__grid{
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-gap: 1em;
    margin-bottom: 2em;
    @include from(m){
      grid-template-columns: repeat(2,1fr);
      grid-gap: 2em;
    }
    @media screen and (orientation: landscape) and (max-height: 650px) {
      margin-bottom: 1.5em;
    }
  }
  &__form{
    margin: 0;
    input,textarea{
      display: block;
      width: 100%;
      line-height: 2em;
      padding: 0.5em;
      font-family: inherit;
      margin-bottom: 1em;
      background-color: transparent;
      border: 1px solid #fff;
      border-radius: .2rem;
      color: #fff;
      &::placeholder{
        color: #fff !important;
      }
      @media screen and (orientation: landscape) and (max-height: 650px) {
        line-height: 1.2em;
      }
    }
    input{

    }
    textarea{
      line-height: 1;
      height: 7em;
      resize: none;
      @media screen and (orientation: landscape) and (max-height: 650px) {
        height: 5em;
      }
    }
    button{
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background: red;
      color: #fff;
      border-radius: 20px;
      border: none;
      line-height: 3em;
      height: 3em;

      transition: background .3s;
      img{
        height: 3em;
      }
      &:hover{
        background: darken(red, $amount: 10);
      }
      @media screen and (orientation: landscape) and (max-height: 650px) {
        line-height: 2em;
        height: 2em;
        img{
          height: 2em;
        }
      }
    }
  }
  &__texto{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    font-size: .8em;
    p{
      margin-bottom: .2em;
    }
  }
}
.empresas{
  @extend %list-style;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 2em;
  > *{
    font-size: .8em !important;
  }
  li{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 1em;
    //font-size: .9em !important;
    @media screen and (orientation: landscape) and (max-height: 650px) {
      //font-size: .8em;
    }
    p{
      width: 80%;
      text-align: center;
      margin-bottom: 0;
      line-height: 1;
    }
    img{
      width: auto;
      height: 90px;
      margin-bottom: 10px;
      @media screen and (orientation: landscape) and (max-height: 650px) {
        height: 50px;
      }
    }
  }
}
.telefonos{
  @extend %list-style;
}
