.contacto__texto{
  img{
    width: 150px !important;
    margin-bottom: 1em;
  }
  a{
    color: red;
  }
}

.mobile #contacto2{

  .section__container, .contacto__texto{
    align-items: center;
    text-align: center;
  }
  .contacto__texto{
    margin-bottom: 1.5em;
  }
  .empresas {
    position: relative;
    display: block;
    width: 100vw;
    height: 32vh;
    li{
      justify-content: space-around;
      width: 100%;
      height:100%;
      position: absolute;
      top: 0;
      left: 0;
      visibility: hidden;
      transition: opacity .8s;
      opacity: 0;
      z-index: 5;
      &.is-active{
        opacity: 1;
        visibility: visible;
        z-index: 10;
      }
    }
  }
}
@include from(768px) {
  .mobile {
    .eg{
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }
    .empresas{
      width: 100% !important;
      position: relative;
      height: 250px;
      li{
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        opacity: 0;
        padding: 0;
        width: 100%;
        &.is-active{
          opacity: 1;
          z-index: 10;
        }
      }
    }

  }
}
.empresas_title, p.tel{
  font-size: 1em !important;
}
