.vehicular {
  background: url("../img/vehicular-m.png") top center / cover no-repeat;

  @media screen and (orientation: landscape) {
    background: url("../img/vehicular.png") top center / cover no-repeat;
  }

  &>.section__container {
    justify-content: center;
    align-items: flex-start;
    // padding-right: 50%;
    // @media screen and (orientation: portrait) {
    //   padding-right: 1em;
    // }
  }

  & .section-title {
    color: red;
  }
  &__grid{
    width: 80%;
    height: 70vh;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 2em;
    margin-bottom: 1em;
    overflow: auto;
    padding: 2vw;
    position: relative;
    z-index: 10;
    &::-webkit-scrollbar{
      background: transparent !important;
    }
    @include to(m){
      grid-template-columns: repeat(2,1fr);
      height: 40vh;
      width: 100%;

    }
    @include from(768px){
      .mobile & {
        width: 100%;
        //height: 50vh;
        grid-template-columns: repeat(3,1fr);
      }
    }
  }
  &__item{
    position: relative;
    transform-origin: center;
    transition: all .3s;
    z-index: 2;
    overflow: hidden;
    border-radius: 1em;
    min-height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: scale(.85);
    &:hover{

      transform: scale(1.05);
    }
    &::after{
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background: rgba(#fff,.2);
      top: 0;
      left: 0;
      z-index: 1;
    }
    img{
      width: 100% !important;
      height: auto !important;
      position: relative;
      z-index: 3;
    }
  }
  &__text{
    text-align: center;
    font-size: 1em;
    width: 100%;
  }
}
