.otros {
  &>.section__container {
    justify-content: center;
    align-items: flex-start;
  }
  .section-title{
    color: #fff;
  }
  &__grid {
    position: relative;
    z-index: 10;
    display: grid;
    grid-template-rows: 60% 1fr;
    width: 100%;
    grid-gap: .5em;
    @include from(l){
      //display: grid;
      grid-template-columns: repeat(2,1fr);
      grid-template-rows: auto;
      grid-gap: 2em;

    }
    @include to(m){
      height: 70vh;
      overflow:hidden;
    }
    p{
      //display: none;
    }
  }
  &__left, &__right{
    display: flex;
    align-items: center;
  }
  &__left{
    @include to(m){
      height: 100%;
      overflow-y: auto;
      p{
        height: 100%;
      }
    }
  }
  &__img{
    width: 90% !important;
    @include to(m){
      width: 100% !important;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
}
