.perfil{
  color: #fff;
  background: url("../img/perfil-m.png") center / cover no-repeat;
  font-family: 'Gotham-Light';
  letter-spacing: 2px;
  @media screen and (orientation: landscape) {
    background: url("../img/perfil.png") center / cover no-repeat;
  }
  p{
    margin-bottom: 1em;
    @media screen and (orientation: landscape) and (min-width: 1080px){
      font-size: 1.1em;
    }

  }
  & .section__container{
    justify-content: center;
    align-items: flex-start;
    text-align: justify;
    font-size: .9em;
    @media screen and (orientation: landscape) and (min-width: 1080px){
      padding-right: 50%;
    }
    &.logo-right{
      z-index: 10;
      opacity: 1;
    }
  }
  & .section-title{
    text-align:left;
    color: #fff;
    font-family: 'Gotham-Light';
    letter-spacing: 5px;
    transition: all .8s;
    opacity: 0;
    transform: translateX(-100%);

  }
  &__list {
    @extend %list-style;
    padding-left: 1em;
    @media screen and (orientation: landscape) and (min-width: 1080px){
       font-size: 1.1em;
     }
    li::before{
      content: '- ';
      }
  }
}
.perfil.active .section-title{
    opacity: 1;
    transform: translateX(0);
}
.perfil p, .perfil ul{
  transition: all .8s;
  opacity: 0;
  transform: translateX(100%);
    
}
.perfil.active p, .perfil.active ul{

  opacity: 1;
  transform: translateX(0);
    
}

.perfil__text{
  @include to(l){
    text-align: left;
  }
}
