use:hover {
  fill: #ffffff !important;
  cursor: pointer;
}
use{
  fill: var(--body-bg) !important;
  stroke: rgba(#ffffff,.8) !important;
}
.state--selected use {
  fill: var(--color-estado) !important;
  cursor: pointer;
}
.map{
  width: 100%;
  height: var(--height);
  //@media screen and (min-width: 1080px) and (max-width: 1399px) {
  //  height: var(--height);
  //}
}
.presencia{
  --height: 300px;
  @media screen and (min-width: 1400px) {
    --height: 500px;
  }
  .subtitle{
    text-align: center;
    font-size: 1.05em;
    margin-bottom: 1em;
  }
  .section__container{
    height: 100%;
    justify-content: center;
    align-items: center;
  }
  .section-title{
    text-align: center;
    font-size: 1.5em !important;
  }

  &__grid{
    width: 100%;
    display: grid;
    grid-template-columns: 450px 1fr;
    @include from(1400px){
      grid-template-columns: 300px 1fr;

    }
    .mobile &{
      @include from(768px){
        grid-template-columns: 1fr;
        justify-content: center;
      }
    }
    grid-gap: 10%;
    height: var(--height);
  }
  &__lista{
    @extend %list-style;
    height: inherit;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    li{
      margin-right: 1.5em;
      cursor: pointer;
      font-size: .8em;
      &:hover, &.state--selected{
        color: var(--color-estado);
      }
    }
  }
  &__mapa{
    width: 100%;
    height: var(--height);
    .mobile &{
      @include from(768px){
        display: none;
      }
    }
  }
}
.mapa-container{
  height: inherit;
}
.footer-text{
  font-size: 75%;
  text-align: left;
  align-self: self-start;
  margin-top: .8em;
  opacity: .75;
}
