.bienvenida{
  --video-size: 50vh;
  @include from(l){
    --video-size: 60vh;
  }
  &.remove{
    opacity: 0;
  }
  transition: opacity .8s;
  opacity: 1;
  background: #010101;
  position: fixed;
  z-index: var(--z-modal);
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  & > .container{
    height: 100%;
    width: 100%;
    background: lime;
  }
  &__video{
    width: 100%;
    height: var(--video-size);
  }
}
.main-title{
  position: absolute;
  left: 50%;
  top: calc(50% + (var(--video-size) * .5));
  //bottom: var(--video-size);
  transform: translateX(-50%) translateY(3vh);
  width: 100%;
  text-align: center;
  margin-bottom: 0;
  line-height: 1.5;
  color: var(--color-main);
  font-family: var(--body-font);
  font-weight: 200;
  letter-spacing: 20px;
  & span{
    letter-spacing: 0;
  }
  @include to(m){
    font-size: 1.2em;
    //transform: translateX(calc(-50% + 10px)) translateY(3vh);
  }
}
.btn-ingresar{
  position: absolute;
  top: calc(50% - (var(--video-size) * .5));
  right: calc(10vw + 0);
  transform: translateY(-7.5vh);
  font-family: $heading-font;
  font-size: 2em;
  &::after{
    position: absolute;
    content: "";
    width: 40px;
    height: 35.4px;
    display: block;
    background: url(../img/compass.svg);
    right: 0;
    top: 55%;
    transform: translateX(110%) translateY(-60%) rotate(90deg)
  }
  @include to(m){
    transform: translateY(-10vh) translateX(-5vw);
  }
}
