.inicio{
  > .section__container{
    justify-content: center;
  }
  background: url("../img/puente-transp-m.jpg") center / cover no-repeat;
  @media screen and (orientation: landscape) {
    background: url("../img/puente-transp.jpg") center / cover no-repeat;
  }
  h2 {
    font-size: 7vw;
    line-height: 1;
    display: table;
    // opacity: ;
    // transform: translateY(-200px);
    letter-spacing: 2px;
    transition: all .5s .2s;
    font-family: 'antonioregular';

    @media screen and(orientation: portrait) {
      font-size: 7vw;
    }

    @media screen and(orientation: landscape) and (min-width: 1024px) {
      font-size: 5vw;
    }

    span {
      text-align: right;
      display: block;
      font-size: 30%;
      font-family: $body-font;

      @media screen and(orientation: portrait) {
        font-size: 45%;
      }
    }

  
  }
}
.slogan {
  transform: translateY(-400%);
  opacity: 0;
  transition: all .5s;

  @media screen and (orientation: landscape) {
    transform: translateY(-200%);
  }

  .active & {
    transform: translateY(0);
    opacity: 1;

  }
}
