.modal{
  width: 100vw;
  height: 100vh;
  background-color: rgba(#000,.9);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: calc(var(--z-modal) * 1000);
  opacity: 0;
  animation: showModal .5s forwards;
  overflow: hidden !important;
  > .contacto__form{
    width: 85%;
  }
  &.modalForm{
    background: rgba(#000,.95);
  }
  img{
    background: #fff;
    border-radius: 1rem;
  }
  @keyframes showModal {
    to{
      opacity: 1;
    }
  }
  &__close{
    $size: 2rem;
    height: $size;
    width: $size;
    position: absolute;
    top: 5vh;
    right: 5vw;
    cursor: pointer;
    @include to(m){
      top: 7.5vh;
      right: 7.5vw;
    }
    &::after, &::before{
      content: '';
      width: 100%;
      height: 2px;
      background-color: #fff;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%) rotate(-45deg);
    }
    &::after{
      transform: translateY(-50%) rotate(45deg);

    }
  }
}
.modal-img{
  cursor: pointer;
}

.modal-proyectos{
  position: absolute;
  background: transparent;
  color: #fff;
  width: 40vw;
  height: auto;
  top: calc(var(--header-height) + 1rem);
  right: 7.5vw;
  transform: translateY(-50%);
  animation: showProyectos .5s forwards;
  background: $body-bg;
  border: 1px solid var(--color-estado);
  border-radius: .5rem;
  @include to(l){
    width: 80vw;
  }
  .proyectos__list{
    width: 100% !important;
    li{
      margin-bottom: 1em;

    }
    p{
      margin-bottom: .2em;
      span{
        font-style: italic;
      }
    }
  }
  @keyframes showProyectos {
    to{
      transform: translateY(0);
      opacity: 1;
    }
  }
  .section-title{
    color: var(--color-estado);
    font-size: 1.2em !important;
  }
  &__close{
    $size: 1rem;
    height: $size;
    width: $size;
    position: absolute;
    top: 1em;
    right: 1em;
    cursor: pointer;
    &::after, &::before{
      content: '';
      width: 100%;
      height: 2px;
      background-color: white;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%) rotate(-45deg);
    }
    &::after{
      transform: translateY(-50%) rotate(45deg);

    }
  }
  &__container{
    padding: 1.5em 1em;
    height: inherit;

  }
  .section-title{
    text-align: center;
  }
  .table-container{
    height: auto;
    max-height: 39vh;
    width: 100%;
    padding: 1em;
  }
  table{
    text-align: center;
    font-size: .8em;
    min-width: 1400px;
  }
  th:first-child, td:first-child{
    width: 3rem;
  }
}

.table-container::-webkit-scrollbar {
  width: 10px;
}

/* Estilos barra (thumb) de scroll */
.table-container::-webkit-scrollbar-thumb {
  background: darkred;
  border-radius: 10px;
}

.table-container::-webkit-scrollbar-thumb:active {
  background-color: #999999;
}

.table-container::-webkit-scrollbar-thumb:hover {
  background: darkred;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

/* Estilos track de scroll */
.table-container::-webkit-scrollbar-track {
  background: #111;
  border-radius: 10px;
  border: 3px solid #111;
  box-shadow: 0 0 0 3px #111;
  cursor: pointer;
}

.table-container::-webkit-scrollbar-track:hover,
.table-container::-webkit-scrollbar-track:active {
  background: #111;
}
