.main-nav{
  position: fixed;
  width: 30vw;
  height: 100%;
  padding-top: calc(var(--header-height) + 2em);
  padding-right: 2vw;
  background-color: #222;
  z-index: calc(var(--z-fixed) - 10);
  top: 0;
  right: 0;
  font-size: 1.2em;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  overflow-y: auto;
  transform: translateX(100%);
  transition: transform .5s linear;
  @media screen and (orientation: portrait) {
    align-items: center;
    width: 100%;
    padding-right: 1em;
    padding-left: 1em;
  }
  &.is-active{
    transform: translateX(0);
  }
}
.main-menu, .main-menu__submenu{
  @extend %list-style;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  @media screen and (orientation: portrait) {
    align-items: center;
  }
  a{
    display: block;
    width: 100%;
  }
}
.main-menu{
  width: 90%;
  > li {
    width: 100%;
    text-align: right;
    margin-bottom: 1em;
    a{
      line-height: 2em;
    }
    @media screen and (orientation: portrait) {
      text-align: center;
      //border: 1px solid rgba(#fff,.05);
    }
  }

  @media screen and (orientation: portrait) {
  }
}
.main-menu__submenu{
  overflow: hidden;
  transition: max-height .5s;
  max-height: 0;
  li {
    margin-bottom: .5em;
    background: #111;
    width: 100%;

    &:last-child {
      margin-bottom: 0;
    }
  }
  &.is-active{
    max-height: 2em * 10;
  }
}
