.nosotros{
  .section-title{
    margin-bottom: 2vh;
    text-shadow: 3px 3px 0px #000;
    transform-origin: center;
    transform: scale(.1);
    transition: all .75s;
    @media screen and (orientation: landscape) {
      margin-bottom: 7vh;
    }
    
  }
  background: url("../img/nosotros-m.jpg") center / cover no-repeat;

  @media screen and (orientation: landscape) {
    background: url("../img/nosotros.jpg") center / cover no-repeat;
  }
  & .section__container{
    @media screen and (orientation: landscape) {
      justify-content: center;
    }
  }
  &__logos{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1em;
    margin-top: 1em;
    @media screen and (orientation: landscape) and (min-width: 1080px){
      margin-top: 5vh;
      width: 90%;
    }
    a, img{
      width: 100%;
      height: auto;
    }
    a{
      transform-origin: center;
      transition: transform .3s;
      transform: scale(1);
      &:hover{
        transform: scale(1.2);

      }
    }
  }
}

.grid{
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
  align-items: center;
  @media screen and (orientation: landscape) and (min-width:1080px){
    grid-template-columns: repeat(2,1fr);
    grid-gap: .5vw;
  }
  h3{
    font-size: 2em;
    line-height: 1;
    margin: 0;
    font-family: 'antonioregular';
    @media screen and (orientation: landscape) {
      font-size: 4em;
    }
    @media screen and (orientation: portrait) {
      text-align: center;
      width: 100%;
    }
    //@media screen and (max-height: 665px) {
    //  font-size: 1.5em;
    //}
  }
  .title-container{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 7.5vw;
  }
  .small{
    margin: 0;
    font-size: .9em;
    //width: 100%;
    @media screen and (orientation: portrait) {
      text-align: center;
      margin-left: 0;
    }
    @media screen and (min-width: 1400px) {
      //margin-left: 12vw;
    }
    @media screen and (max-height: 665px) {
      font-size: .7em;
    }
    // text-align: right;
  }
}
.nosotros__texto{
  font-family: 'Gotham-Light';
  font-size: 1.1em;
  @media screen and (orientation: portrait) {
    text-align: center;
    font-size: 1em;
  }
  @media screen and (max-height: 665px) {
    font-size: 1.1em;
  }
}
.nosotros.active .section-title {
  opacity: 1;
  transform: scale(1);
  font-family: 'Gotham-Light';
  @media screen and (orientation: portrait) and (max-width: 600px){
    font-size: 1.4em !important;
  }
}

.grid__left{
  transition: transform .5s;
  transform: translateX(-100%);
  .subtitle{
    font-family: 'Gotham-Light';
  }
}
.nosotros__texto{
  transition: transform .5s;
  transform: translateX(100%);
  p{
    font-size: .9em!important;
  }
}
.active .grid__left, .active .nosotros__texto{
  transform: translateX(0);
}
.mobile {
  @include from(768px){
    .section__container {
      padding-left: 10vw !important;
      padding-right: 10vw !important;
    }
  }
  .nosotros {

    @include from(768px) {
      .section__container{
        justify-content: center;
        padding: calc(var(--header-height) + 2em) 10vw calc(var(--header-height) + 2em);
      }
      .section-title{
        margin-bottom: 3em;

      }
      .grid__left{

      }
      .nosotros__logos{
        margin-bottom: 2em;
      }
      .title-container{
        align-items: center;
        margin-right: 0;
        margin-bottom: 2em;
        font-size: 1.5em;
      }
    }
  }
}
