.section{
  position: relative;
}
.section__container{
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: calc(var(--header-height) + 1em) 1em 1em;
  @media screen and (orientation: landscape) {
    padding: calc(var(--header-height) + 2em) 10vw 2em;
  }
}
.section-title{
  font-family: 'antoniolight','serif';
  color: $color-fisrt;
  text-transform: uppercase;
  letter-spacing: 1vw;
  font-size: 2em !important;
  // margin-bottom: 0;
  @media screen and (orientation: landscape) {
    font-size: 2vw;
  }
}
.bienvenida {
  position: fixed;
  width: 100vw;
  height: 100vh;
  //background-color: crimson;
  z-index: calc(var(--z-modal) * 1000);

  & .section__container {
    justify-content: center !important;
  }
}
.section__container.logo-right{
  position: relative;
  &::after{
    content: '';
    position: absolute;
    display: block;
    height: 100vh;
    width: 25.87vh;
    background: url('../img/logo_fondo-v.png') center / cover no-repeat;
    right: 0;
    top: 0;
    transform: translateX(21%);
    opacity: .3;
    z-index: 1;
  }
}

.compass{
  position: relative;
  overflow: hidden;
  &::before{
    content: '';
    position: absolute;
    background: url(../img/compass_all.png) center / cover no-repeat;
    width: 15vh;
    height: 15vh;
    bottom: 0;
    left: 50%;
    transform: translateY(40%) translateX(-50%);
  }
}
.separador{
  width: 100%;
  height: 25vh;
  background: url(../img/Otros_img002-1024x487.jpg) center / cover no-repeat;
  &.bottom{
    background: url(../img/Otros_img003.jpg) center / cover no-repeat;
  }
}
.main-footer{
  height: var(--footer-height);
  padding: 2em 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: .8em;
  font-family: 'Gotham-Medium', sans-serif;
  text-align: center;
  @media screen and (orientation: landscape) and (min-width: 1024px) {
    text-align-last: left;
    padding: 2em 10vw;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  @media screen and (orientation: portrait) {
    img{
      margin: 0 auto;
    }
    .copy-container{
      margin-bottom: 1rem;
    }
  }
  img{
    margin-bottom: 1rem;
  }
  p{
    span{
      margin-left: .5em;
      margin-right: .5em;
    }
    line-height: 1;
    margin-bottom: 0;
    a{
      color: #fff;
    }
    &:last-child{
      margin-top: .5em;
    }
  }
  a{
    color: red;
  }
}



