$bd: 5px solid $color-fisrt;
.desktop {
  .software {
    > .section__container {
      padding-top: var(--header-height);
      align-items: center !important;
    }
    &::before{
      content: unset !important;
    }
    & .section-title {
      font-family: 'antonioregular', serif;
      letter-spacing: unset;
      font-size: 3em;
      color: #fff;
      margin-bottom: .5rem;
    }

    background: url("../img/software-m.png") top center / cover no-repeat;

    @media screen and (orientation: landscape) {
      background: url("../img/software.png") top center / cover no-repeat;
    }

    &__grid {

      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 1em;
      margin-bottom: 1em;

    }
  }
  .marco{
    display: table;
    text-align: center;
    font-size: 1.4em;
    width: 80%;
    position: relative;
    margin: 0 auto .5em;
    @media screen and (min-width: 1024px) and (max-width: 1400px) and (orientation: landscape){
      font-size: .8em !important;
    }
    &::after {
      content: '';
      height: 1.5em;
      width: 1.5em;
      position: absolute;
      background-color: transparent;
      top: -.3em;
      left: -.5em;
      border-top: $bd;
      border-left: $bd;
    }
  }
  .ptv-group{
    @extend %list-style;
    font-size: 1rem;
    margin-bottom: .5rem;
    img{
      width: 25%;
      margin: 0 auto .5em;
    }
    span{
      color: #F40021;
      font-weight: bold;
    }
    li::before{
      content: '.';
      color: #F40021;
      font-weight: bold;
    }
  }
  .right{
    &__img__software{
      width: 30%;
      margin: 0 auto .5em;
      &:last-child{
        width: 55%;
        margin-bottom: 0;
      }
    }
  }
  .software__footer{
    width: 100%;
    .t-c{
      text-align: center;
      font-weight: bold;
      line-height: 1;
      margin-bottom: 1em;
      font-size: 1.1em;
    }
    .paquetes{
      @extend %list-style;
      margin-left: .5rem;
      text-align: left;
      font-size: 1rem !important;
      span{
        color: #f1a000;
        //color: #8C8100;
      }
    }
  }
}
.pix4d{
  width: 75px !important;
  margin: 1em auto 1em;
}
.paquetes.marco::after{
 border-color: #fff;
}
.software .title{
  text-align: left;
  align-self: flex-start;
  margin-bottom: 2em;
}

.software .section__container{
  @media screen and (orientation: landscape) and (max-width: 1500px) {
    transform-origin: center;
    transform: scale(.9);
    max-height: 100vh !important;
  }
}
