.modal {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: calc(var(--z-modal) * 1000);
  opacity: 0;
  animation: showModal .5s forwards;
  overflow: hidden !important;
