@media screen and (orientation: landscape) {
  .mobile body{
    overflow: hidden !important;
    &::after {
      position: fixed;
      width: 100vw;
      height: 100vh;
      color: #000;
      content: "Por favor gire el dispositivo a posición vertical";
      background: url(../img/mobile-rotate-rotation.svg) center / 10% no-repeat, #fdfdfd,;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      top: 0;
      left: 0;
      z-index: calc(var(--z-modal) * 1500);
      padding-top: 25vh;
      font-size: 1.5em;
      font-weight: bold;
    }
  }
}
 p, li, .valores p{
   font-size: 1rem !important;
 }
@include to(l){
  .presencia__lista li, .footer-text{
    font-size: .8rem !important;

  }
}

.nextPage{
  $size: 3.5vh;
  height: $size;
  width: $size * 1.5;
  //background: lime;
  position: fixed;
  display: block;
  bottom: 2vh;
  left: 50%;
  transform: translateX(-50%);
  z-index: calc(var(--z-modal) + 100);
  &::before, &::after{
    content: '';
    position: absolute;
    top: 0;
    left: 25%;
    width: 2px;
    height: 100%;
    background: #fff;
    transform-origin: center;
    transform: rotate(-45deg);
  }
  &::after{
    left: unset;
    right: 25%;
    transform: rotate(45deg);
  }
}
.prevPage {
  display: none;
  bottom: unset;
  top: 3vh;
  transform: translateX(-50%) rotate(180deg);
  @include to(380px){
    top: calc(var(--header-height) + .5em);
  }
}
.main-logo{
  position: absolute;
  height: auto;
  width: 75%;
  max-width: 300px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
  @include from(l){
    width: 100%;
    max-width: 450px;
  }
}
.marcaagua {
  opacity: .5;
  position: absolute;
  bottom: 0;
  font-size: .8em !important;
}
.agro__left, .responsabilidad__datos{
  position: relative;
}
.main-menu__link:last-child{
  line-height: 1.1 !important;
}
.scroll-ele, .agro-text, .responsabilidad__text{
  padding-bottom: 2em !important;
}
